.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mt-15 {
  margin-top: 15px;
}
table th {
  text-align: center;
}
table td {
  text-align: center;
  vertical-align: middle !important;
}
.label:hover {
  cursor: pointer;
}
.checked {
  color: red !important;
}
.fa-check {
  color: green;
}
.fa-times {
  color: red;
}
.label, table {
  user-select: none;
}